<template>
  <div ref="trys"  style="height: 100%">
      <div v-if="showInfo" class="info-container">
        <info :row="row" @closeDiv="closeDiv" ref="infoDetail"></info>
      </div>
      <div class="all-container" v-if="!showInfo">
        <div class="operate-div">
          <template v-for="(item, $index) in formList">
            <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom" style="margin-right:1.5em"></inputs>
            <datePicker ref="timer" style="margin-left:10px" v-if="item.type == 'timer'"    type= "doubleDate"  :dateOpt="item.options"
                        @getDatePicker="getFrom"></datePicker>
          </template>
        </div>
        <div ref="myOwnTable" class="table-container" style="padding:0 1em;width:calc(100% - 2em);height:calc(100% - 4.5em)" >
          <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow" @rowDbClick="checkInfo"></tables>
        </div>
      </div>
  </div>
</template>
<script>
  import info from './info.vue'
  export default{
    data() {
      return {
        searchParam: {
          beginDate: '',
          endDate: '',
          orgName: ''
        },
        controls: [],
        row:{
          val: "",
          totalElectricity: "",
          date:{
            startDate:"",
            endDate:""
          }
        },
        showInfo: false,
        tableShow:false,
        tableOpt: {
          column: [
            {title: '小区名称', field: 'orgName',textAlign: 'center',width: '200'},
            {title: '电表数量', field: 'ammeterNum', textAlign: 'center', width: '100'},
            {title: '照明插座（KW·h）', field: 'socketElectricity', textAlign: 'center', width: '100'},
            {title: '空调（KW·h）', field: 'airElectricity', textAlign: 'center', width: '100'},
            {title: '动力（KW·h）', field: 'powerElectricity', textAlign: 'center', width: '100'},
            {title: '特殊用电（KW·h）', field: 'specialElectricity', textAlign: 'center', width: '100'}
          ],
          url: 'efficiency/queryVillageList',
          method: 'post',
          // method: 'get',
          // url: 'dist/json/efficiency.json',
          pageSize: 20,
          pageNumber: 1,
          pageList: [20, 40, 60],
          queryParam: {},
          isExport: true,
          exportURL: 'efficiency/downloadVillageList',
        }
      }
    },
    components: {
      info
    },
    computed: {
      formList: function () {
        let dates = this.initDate(true);
        return [
          {
            type: 'timer',
            options: {
              key: 'searchDate',
              value: {startDate: dates.start, endDate: dates.end},
              width: '23em',
              fontSize: '0.8em'
            }
          },
          {
            type: 'input',
            options: {
              type: 'typeSearch',
              key: 'searchParam',
              selectOpt: {
                width: '7em',
                cs: 'client',
                data: [
                  {text: '小区名称', value: 'orgName'},
                ]
              }
            }
          }
        ]
      },
    },
    methods: {
      checkInfo: function (val) {
        this.row.val = val.val;
        this.row.totalElectricity = val.val.socketElectricity + val.val.airElectricity + val.val.powerElectricity + val.val.specialElectricity;
        this.row.date.startDate = this.searchParam.beginDate.split("-").join("/")
        this.row.date.endDate = this.searchParam.endDate.split("-").join("/")
        this.showInfo = true;
      },
      closeDiv: function () {
        this.showInfo = false;
        this.getParams();
      },
      getParams: function (pageNumber) {
        // let arr = this.getSearchData(this.searchParam);
        let arr = this.searchParam;
        this.getHeight('calc(100% - 3.5em)');
        this.searchTable(arr, pageNumber);
      },
      getFrom: function (val) {
        console.log(val);
        if (val.combine) {
          if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
            this.searchParam = this.cleanSearchData(this.searchParam, ['beginDate', 'endDate']);
            this.getOption(val, true);
            this.getParams(1);
          }
        } else {
          this.getOption(val, true);
          if(val.key == 'searchDate')  {
            let arr = val.val.split('至');
            this.searchParam.beginDate = arr[0];
            this.searchParam.endDate = arr[1];
          }
          this.getParams(1);
        }
      },
      windowChange: function(){
        let that = this;
        this.getHeight();
        // window.onresize = function () {
        //   that.getHeight();
        // }
      },
      init: function () {
         this.tableOpt.isExport = this.controls[6];
         this.tableShow = true;
         this.windowChange()
      }
    },
    mounted: function() {
      this.getAction();
    },
    created: function () {
      let dates = this.initDate(true);
      this.tableOpt.queryParam['beginDate'] = this.searchParam.beginDate = dates.start;
      this.tableOpt.queryParam['endDate'] = this.searchParam.endDate = dates.end;
      this.tableOpt.queryParam['orgName'] = this.searchParam.orgName;
     
    }
  }
</script>
<style lang="scss">

</style>



