<template>
  <div  class="info-div EA-info">
    <div class="info-header">
      <span class="callback" @click="resetComponent">能效分析 </span>
      <span> > 能效详情 ( {{row.val.orgName}} )</span>
    </div>
    <div class="info-containerinfo-container" style="height: calc(100% - 1em);margin-top: 1em">
      <div class="list-content  list-contentB" style="padding:0 0.5em 0 0.5em">
        <div class="list-header noborder-ListHeader" style="width: 100%">
          <div class="topChartCon">
            <div class="basic-info">
              <div class="head-title">
                <img src="dist/img/efficiencyAnalysis/village.png" alt="位置">
                <span>小区基础信息</span>
              </div>
              <div class="basic-content">
                <ul class="detail">
                  <li v-for="item in basicInfo">
                    <div class="title">{{item.title}}:</div>
                    <div class="value">{{item.value}}</div>
                  </li>
                </ul>
              </div>
            </div>
            <div class="pie">
              <p>{{row.date.startDate}}-{{row.date.endDate}} <span>总电量：{{row.totalElectricity}} (KW·h)</span></p>
              <div ref="pie" style="width: 100%;height: 100%"></div>
            </div>
            <div class="bar">
              <div class="title">
                <span v-for="(item, $index) in tabs"
                      :class="{action: tabIndex===$index}"
                      @click="getTab($index, item)">{{item.title}}</span>
              </div>
              <div class="mychar" ref="mychar">
                <div style="width: 100%;text-align: center;font-size: 0.8em">
                  <img src="/dist/img/empty-data.png" alt="">
                  <p>查不到相应的数据</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="list-content  list-contentB" style="padding:0 0.5em 1em 0.5em;height:calc(100% - 16em)">
        <div class="list-header noborder-ListHeader">
          <i class="hasIcon hasImg">
            <img src="dist/img/widsomApp/table_list.png" />
          </i>
          设备耗电能效
        </div>
        <div class="operate-div" style="margin-right: 0.2em">
          <template v-for="(item, $index) in formList">
            <datePicker ref="timer" style="margin-left:10px" v-if="item.type == 'timer'" type= "doubleDate"  :dateOpt="item.options"
                        @getDatePicker="getFrom"></datePicker>
          </template>
        </div>
        <div ref="myOwnTable" class="table-container" style="padding:0 1em;width:calc(100% - 2em);height:calc(100% - 3.5em)" >
          <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow" @rowDbClick="checkInfo"></tables>
        </div>
      </div>
    </div>
    <dialogs type="customer" ref="dialogsDetail" width="70em" :maxHeight="maxHeight" :height="divHeight" minWidth="1100px">
      <div slot="header">
        <div class="head">
          <span>设备分类能效-{{title}}</span>
          <img class="dialog-img" src="dist/close-icon.png" @click="cancelDialog" title="关闭"/>
        </div>
      </div>
      <div slot="content" style="height: 100%">
        <div class="EA-content" ref="EAcontent">
          <div class="con-box">
            <div class="detailContent">
              <div ref="myOwnTable1" class="table-container" style="height: 100%;width:calc(100% - 0em);padding-left: 0;position: relative" >
                <tables ref="myTable1" :tableOpt="detailTabOpt" class="tablePadding" @rowClick="checkInfo"  @getOtherData="getTotals" v-if="isDetail">
                  <div slot="other">
                    <div class="bg" ref="tbBg" :style="setStyle" v-if="noConnect"></div>
                    <div class="bg-Bridge" ref="Bridge" v-if="noConnect"></div>
                  </div>
                </tables>
              </div>
            </div>
            <div class="box-right" ref="boxRight">
              <div class="lineChar" ref="lineChar"></div>
              <div class="nodata-Char" v-if="tableData.length === 0">
                <img src="/dist/img/empty-data.png" alt="" style="height: 80%">
                <p>查不到相应的数据</p>
              </div>
              <div class="all_foot_con">
                <table class="all_foot_ta">
                  <tr style="background: #305ea0;border: none">
                    <td width="20%">时间</td>
                    <td width="20%">总电量(KW·h)</td>
                    <td width="20%">峰电量(KW·h)</td>
                    <td width="20%">平电量(KW·h)</td>
                    <td width="20%">谷电量(KW·h)</td>
                  </tr>
                </table>
                <div class="overflow" style="width: calc(100% + 0px);height: calc(100% - 1em);overflow: hidden">
                  <VuePerfectScrollbar style="position: relative;height:calc(100% - 1em);width:100%;">
                    <table class="all_foot_ta tab-tr" v-if="tableData.length>0">
                      <tr v-for="(item, $index) in tableData">
                        <td width="20%">{{item.time}}</td>
                        <td width="20%">{{item.readValue}}</td>
                        <td width="20%">{{item.readValue2}}</td>
                        <td width="20%">{{item.readValue3}}</td>
                        <td width="20%">{{item.readValue4}}</td>
                      </tr>
                    </table>
                    <div style="width: 100%;text-align: center;font-size: 0.8em" v-if="tableData.length===0">
                      <img src="/dist/img/empty-data.png" alt="">
                      <p>查不到相应的数据</p>
                    </div>
                  </VuePerfectScrollbar>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </dialogs>
  </div>
</template>
<script>
  export default{
    data() {
      return {
        isDetail: false,
        position: [0,0],
        tableData:[],
        taburl:"dist/json/lineloss.json",
        tableShow: false,
        bgPage: 1,
        noConnect: false,
        tabIndex: 2,
        trIndex: 0,
        typeChars: "",
        lineChar: "",
        pie: "",
        height:"",
        divHeight: "",
        range: "",
        maxHeight:"45.5em",
        clientWidth: "",
        once:false,
        title: "照明插座",
        lossType: "", // 耗电类型
        countDate: "", // 用于查询最近7天的日期
        left: "",
        tabs:[{title: "年", url:""},{title: "月", url:""},{title: "日", url:""}],
        platformAreaNum:"",
        searchParam: {
          beginDate: "",
          endDate: "",
          orgNo: ""
        },
        tableOpt:{
          column: [
            {title: '序号', field: 'Index',textAlign: 'center',width: '5%'},
            {title: '时间', field: 'countDate',textAlign: 'center',width: '16%'},
            {title: '总电量', field: 'totalElectricity', textAlign: 'center', width: '15%'},
            {title: '设备数量', field: 'socketNum', textAlign: 'center', width: '8%'},
            {title: '总耗电量', field: 'socketElectricity', textAlign: 'center', width: '8%',type: "object",
              formatter: function (val, index, row, obj) {
                let template;
                template = '<span class="list-operate"  @click="openAccount" style="color:#C68F3C;display: block;width: 100%;height: 100%">' + val +'</span>';
                return {
                  template: template,
                  methods: {
                    openAccount: function () {
                      obj.$parent.openDialogs(index, row);
                    }
                  }
                }
              }},
            {title: '设备数量', field: 'airNum', textAlign: 'center', width: '8%'},
            {title: '总耗电量', field: 'airElectricity', textAlign: 'center', width: '8%',type: "object",
              formatter: function (val, index, row, obj) {
                let template;
                template = '<span class="list-operate"  @click="openAccount" style="color:#C68F3C;display: block;width: 100%;height: 100%">' + val +'</span>';
                return {
                  template: template,
                  methods: {
                    openAccount: function () {
                      obj.$parent.openDialogs(index, row);
                    }
                  }
                }

              }},
            {title: '设备数量', field: 'powerNum', textAlign: 'center', width: '8%'},
            {title: '总耗电量', field: 'powerElectricity', textAlign: 'center', width: '8%',type: "object",
              formatter: function (val, index, row, obj) {
                let template;
                template = '<span class="list-operate"  @click="openAccount" style="color:#C68F3C;display: block;width: 100%;height: 100%">' + val +'</span>';
                return {
                  template: template,
                  methods: {
                    openAccount: function () {
                      obj.$parent.openDialogs(index, row);
                    }
                  }
                }

              }},
            {title: '设备数量', field: 'specialNum', textAlign: 'center', width: '8%'},
            {title: '总耗电量', field: 'specialElectricity', textAlign: 'center', width: '8%',type: "object",
              formatter: function (val, index, row, obj) {
                let template;
                template = '<span class="list-operate"  @click="openAccount" style="color:#C68F3C;display: block;width: 100%;height: 100%">' + val +'</span>';
                return {
                  template: template,
                  methods: {
                    openAccount: function () {
                      obj.$parent.openDialogs(index, row);
                    }
                  }
                }

              }},
          ],
          url: 'efficiency/queryDeviceList',
          method: 'post',
          // method: 'get',
          // url: 'dist/json/queryDeviceList.json',
          pageSize: 20,
          pageNumber: 1,
          pageList: [20, 40, 60],
          queryParam: {},
          isExport: true,
          exportURL: 'efficiency/downloadDeviceList',
          istotalhead: true,
          totalhead:[
            {title: '照明插座(KW·h)', totalFieldield: ['socketNum', 'socketElectricity'], field:"socketElectricity",textAlign: 'center', width: '16%'},
            {title: '空调(KW·h)', totalFieldield: ['airNum', 'airElectricity'], field:"airElectricity",textAlign: 'center', width: '16%'},
            {title: '动力(KW·h)', totalFieldield: ['powerNum','powerElectricity'], field:"powerElectricity" ,textAlign: 'center', width: '16%'},
            {title: '特殊用电(KW·h)', totalFieldield: ['specialNum','specialElectricity'], field:"specialElectricity",textAlign: 'center', width: '16%'},
          ]
        },
        detailTabOpt: {
          column: [
            {title: '序号', field: 'Index',textAlign: 'center',width: '20'},
            {title: '用户名称', field: 'customerName',textAlign: 'center',width: '100'},
            {title: '表号', field: 'meterNo', textAlign: 'center', width: '100'},
            {title: '用电量(KW·h)', field: 'power', textAlign: 'center', width: '100'}
          ],
          method: 'post',
          tableData:[],
          // url: 'dist/json/queryDeviceDetail.json',
          url: 'efficiency/queryDeviceDetail',
          pageSize: 8,
          pageNumber: 1,
          queryParam:{},
          exportURL: 'efficiency/downloadDeviceDetail',
          isExport: true,
        },
      }
    },
    props: ['row'],
    computed: {
      formList: function () {
        let dates = this.initDate(true);
        return [
          {
            type: 'timer',
            options: {
              key: 'searchDate',
              value: {startDate: dates.start, endDate: dates.end},
              width: '23em',
              fontSize: '0.8em'
            }
          }
        ]
      },
      basicInfo: function () {
        let detail = this.row;
        return [
            {
                title: "小区名称",
                value: detail.val.orgName
            },
          {
            title: "台区数",
            value: this.platformAreaNum
          },  {
            title: "电表数",
            value: detail.val.ammeterNum
          }
        ]
      },
      setStyle: function () {
        let top = this.trIndex * this.height;
        let topPx = this.trIndex * (this.range + 0.5);
        return "top:calc(" + top + "em + " + topPx + 'px);padding:' + this.range/2 +'px 0;height:' + this.height + 'em;';
      }
    },
    watch: {
      'detailTabOpt.pageNumber': {
        handler(val, oldVal) {
          if(val != this.bgPage){
            this.noConnect = false;
          }else{
            this.noConnect = true;
            let self = this;
            self.setBridgeTop(true);
          }
        },
        deep: true
      },
    },
    methods: {
      getTotals: function () {
        let self = this;
        if(this.once){
          this.getData({url: "efficiency/queryDeviceDetail", param:{
              pageNumber: 1,
              pageSize: 1,
              orgNo: self.row.val.orgNo,
              countDate: self.countDate,
              usePowerType: self.lossType
            },
            callback:function(data){
              if(data.length>0){
                self.getDetailData(data[0].meterUserNo)
              }
              self.once = false;
            }})
        }
      },
      setBridgeTop: function (flag) {
        var self = this;
        this.$nextTick(function () {
          self.$refs.Bridge.style.top = self.$refs.tbBg.getBoundingClientRect().top + 'px'
          self.$refs.Bridge.style.left = self.$refs.tbBg.getBoundingClientRect().left + self.$refs.EAcontent.getBoundingClientRect().width/2  - 10 + 'px'
          self.$refs.Bridge.style.padding = self.range/2 +'px 0'
          self.$refs.Bridge.style.height = self.height + 'em'
        })
      },
      getDetailData:function(meterUserNo){
        var self = this;
        let param = {
          orgNo: self.row.val.orgNo,
          usePowerType: self.lossType,
          meterUserNo: meterUserNo,
          countDate : self.countDate ,
        }
        self.getRequest({
          url: "efficiency/lastPowerDetail",
          method: 'post',
          param: param,
          success: function (data) {
              self.tableData = data.data;
              self.noConnect = true;
              self.setlineChars();
              self.setBridgeTop();
          },
          error: function (){

          }
        });
      },
      checkInfo: function (obj) {
        var self = this;
        this.bgPage = this.detailTabOpt.pageNumber;
        this.noConnect = true;
        this.trIndex = obj.index;
        setTimeout(function () {
          self.setBridgeTop();
          self.getDetailData(obj.val.meterUserNo)
        },0)

      },
      cancelDialog: function () {
        this.isDetail = false;
        this.$refs.dialogsDetail.close();
        this.noConnect = false;
        this.once = false;
        this.trIndex = 0;
        this.tableData = [];
        this.bgPage = 1;
      },
      openDialogs: function (index,row) {
        let self = this;
        let lossData = this.tableOpt.totalhead;
        let lossType = 0;
        for(let i = 0; i < lossData.length; i++){
          if(lossData[i].totalFieldield.indexOf(index) !== -1){
            switch (i){
              case 0:
                lossType = 1;
                self.title = "照明插座 " + row.countDate
                break;
              case 1:
                lossType = 2;
                self.title = "空调 " + row.countDate
                break;
              case 2:
                lossType = 3;
                self.title = "动力 " + row.countDate
                break;
              case 3:
                lossType = 4;
                self.title = "特殊用电 " + row.countDate
                break;
            }
          }
        }
        this.lossType = lossType;
        this.countDate = row.countDate;
        this.detailTabOpt.queryParam['orgNo'] = this.searchParam.orgNo
        this.detailTabOpt.queryParam['usePowerType'] = this.lossType
        this.detailTabOpt.queryParam['countDate'] = row.countDate
        this.detailTabOpt.pageNumber = 1;
        self.$refs.dialogsDetail.show();
        self.isDetail = true;
        setTimeout(function () {
          self.$refs.myTable1.onResize(false, self.$refs.myOwnTable1);
          self.once = true;
        },0)
      },
      setChars: function (data) {
        var self = this;
        var option = {
          tooltip : {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          legend: {
            data:['当前'],
            borderRadius: 11,
            icon: 'roundRect',
            itemGap:50,
            itemWidth: 35,
            itemHeight: 8,
            textStyle : {
              color: 'rgba(255,255,255, 0.9)'
            }
          },
          grid: {
            left: '5%',
            right: '2%',
            bottom: '0%',
            top: '20%',
            containLabel: true
          },
          xAxis : [
            {
              type : 'category',
              boundaryGap : [1,1],
              data : [],
              axisLine:{
                show: true,
                lineStyle:{
                  color: '#fff'
                }
              },
              axisTick:{
                show: false
              },
            }
          ],
          yAxis : [
            {
              name : '电量(KW·h)',
              nameTextStyle:{
                color: "#fff",
                width:100
              },
              type : 'value',
              axisLine: {
                show: false,
                lineStyle:{
                  color: '#3C9BF9'
                }
              },
              axisTick: {
                show: false
              },
              splitLine: {
                show: true,
                lineStyle: {
                  color: ['#7E869B']
                }
              },
              axisLabel: {
                show: true,
                formatter:'{value}',
                textStyle: {
                  color: '#fff'
                }
              },
            }
          ],
          series : [
            {
              name:'当前',
              type:'bar',
              stack: '当前',
              lineStyle:{
                color: '#398FE6'
              },
              itemStyle: {
                color: '#398FE6',
                borderWidth: 3
              },
              barWidth: 8,
              barGap: 0,
              data:[]
            }
          ]
        };
        if(this.tabIndex != 0){
          option.legend.data.push("同期");
          option.series.push({
            lineStyle:{
              color: '#E69039'
            },
            itemStyle: {
              color: '#E69039',
              borderWidth: 3
            },
            name:'同期',
            type:'bar',
            stack: '同期',
            barWidth: 8,
            barGap: 0,
            data:[]
          })
          option.series[1].data = data.yLastData;
        }
        switch (this.tabIndex){
          case 0:
            option.tooltip.formatter = '{b}年用电量<br />{a}:{c}';
            break;
          case 1:
            option.tooltip.formatter = '{b}月用电量<br />{a}:{c}';
            break;
          case 2:
            option.tooltip.formatter = '{b}号用电量<br />{a}:{c}';
            break;
        }
        option.xAxis[0].data = data.xData;
        option.series[0].data = data.yThisData;
        this.typeChars = echarts.init(self.$refs.mychar);
        this.typeChars.setOption(option, true);
      },
      setPieChar: function () {
        let detail = this.row;
        let self = this;
        let option2 = {
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient:"vertical",
            right: "40",
            align: "left",
            bottom: "30",
            itemGap: 20,
            itemWidth: 10,
            itemHeight: 10,
            textStyle: {
              color: '#fff',
              fontSize: 14
            },
            data:[
              {name:'照明插座',icon: "circle"},
              {name:'空调',icon: "circle"},
              {name:'动力',icon: "circle"},
              {name:'特殊用电',icon: "circle"}
            ]
          },
          calculable : true,
          series : [
            {
              name:'用电量',
              type:'pie',
              startAngle: '135',
              radius : [0, '60%'],
              center : ['40%', '50%'],
              label:{
                show: true,
                color:"#fff",
                formatter:function (params) {
                  return params.name + params.percent + '%';
                },
              },
              data:[
                {value:detail.val.socketElectricity, name:'照明插座',itemStyle: {
                    color: "#398FE6",
                    fontSize: 14
                  }},
                {value:detail.val.airElectricity, name:'空调',itemStyle: {
                    color: "#4AD182",
                    fontSize: 14
                  }},
                {value:detail.val.powerElectricity, name:'动力',itemStyle: {
                    color: "#C4425A",
                    fontSize: 14
                  }},
                {value:detail.val.specialElectricity, name:'特殊用电',itemStyle: {
                    color: "#E9913A",
                    fontSize: 14
                  }}
              ]
            }
          ]
        };
        this.pie = echarts.init(self.$refs.pie);
        this.pie.setOption(option2);
      },
      setlineChars: function () {
        var self = this;
        var option = {
          tooltip : {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          title: {
            left: 'center',
            text: '用电量曲线',
            top: '5%',
            textStyle: {
              color: "#fff",
              fontSize: 14,
            }
          },
          grid: {
            left: '5%',
            right: '5%',
            bottom: '5%',
            top: '20%',
            containLabel: true
          },
          xAxis : [
            {
              type : 'category',
              boundaryGap : [1,1],
              data : [],
              axisLine:{
                show: true,
                lineStyle:{
                  color: '#1F71C9'
                }
              },
              axisTick:{
                show: false
              },
            }
          ],
          yAxis : [
            {
              max: function(value) {
                var a = parseInt((value.max / 3).toFixed(2));
                console.log(value.max)
                return (value.max + a).toFixed(0);
              },
              boundaryGap: true,
              name : '用电量(KW·h)',
              nameTextStyle:{
                color: "#fff"
              },
              type : 'value',
              axisLine: {
                show: false,
                lineStyle:{
                  color: '#3C9BF9'
                }
              },
              axisTick: {
                show: false
              },
              splitLine: {
                show: true,
                lineStyle: {
                  color: ['#7E869B']
                }
              },
              axisLabel: {
                show: true,
                formatter:'{value}',
                textStyle: {
                  color: '#fff'
                }
              },
            }
          ],
          series : [
            {
              name:'用电量',
              type:'line',
              stack: '用电量',
              symbol:'none',
              smooth:true,
              lineStyle:{
                color: "#C73643"
              },
              areaStyle: {
                color: '#634060'
              },
              data:[],
              markPoint : {
                data : [
                  {
                    type : 'max',
                    name: '最大值',
                    symbol: 'image://dist/img/widsomApp/popup_red.png',
                    symbolOffset: [0, '-55%'],
                    label: {
                      normal: {
                        padding: [5,10,15,10],
                        color: '#fff',
                        position: 'inside'
                      }
                    }
                  },
                ]
              },
            }
          ]
        };
        for (let i = 0; i < this.tableData.length; i++){
          option.xAxis[0].data.unshift(this.tableData[i].time);
          option.series[0].data.unshift(this.tableData[i].readValue||0);
        }
        this.lineChar = echarts.init(self.$refs.lineChar);
        this.lineChar.setOption(option, true);
      },
      getTab: function (index, item) {
        var self = this;
        if(index === this.tabIndex){
          return;
        }
        this.tabIndex = index;
        this.getData({url: "efficiency/electricCount", param:{
            model: self.tabIndex,
            orgNo: self.row.val.orgNo
          },
          callback:function(data){
            self.setChars(data.listCount);
          }})
      },
      resetComponent: function () {
        this.$emit('closeDiv');
      },
      getFrom: function (val) {
        this.getOption(val, true);
        if(val.key == 'searchDate')  {
          let arr = val.val.split('至');
          this.searchParam.beginDate = arr[0];
          this.searchParam.endDate = arr[1];
        }
        this.getParams(1);
      },
      getParams: function (pageNumber) {
        let arr = this.getSearchData(this.searchParam);
        this.getHeight();
        this.searchTable(arr, pageNumber);
      },
      windowChange: function () {
        let self = this;
        this.getHeight();
        //window.onresize = function () {
          self.setScreen();
        //  self.getHeight();
          if(self.typeChars) {
             self.typeChars.resize();
          }
          if(self.pie) {
             self.pie.resize();
          }
          try {
            self.$refs.myTable1.onResize(false, self.$refs.myOwnTable1);
            self.lineChar.resize()
            self.setBridgeTop();
          }catch(error) {}

        //}
      },
      setScreen: function () {
        this.clientWidth = document.documentElement.clientWidth;
        let range = 22;
        let height = 2;
        this.divHeight = "auto";
        if(this.clientWidth<=1366){
          range = 13;
          height = 2.5;
          this.divHeight = '470px';
        }else if(1367 <= this.clientWidth && this.clientWidth<=1700){
          range = 16;
          height = 2.3;
        }
        this.range = range;
        this.height = height;
      },
      getData: function(obj){
        var self = this;
        let url = obj.url;
        let param = obj.param || '';
        this.getRequest({
          url: url,
          method: 'post',
          param: param,
          success: function(data) {
            obj.callback(data.data);
          },
          error: function (data){
            self.$refs.alerts.show({msg: data.message});
          }
        });
      }
    },
    mounted: function() {
      var self = this;
      this.getData({url: "efficiency/electricCount", param:{
          model: 2,
          orgNo: self.row.val.orgNo
        },
        callback:function(data){
          self.setChars(data.listCount);
        }})
      this.getData({url: "efficiency/queryMeterNum", param:{
          orgNo: self.row.val.orgNo
        },
        callback:function(data){
          self.platformAreaNum = data;
        }})
      this.windowChange();
      this.setPieChar();
    },
    created: function () {
      this.setScreen();
      let dates = this.initDate(true);
      this.tableOpt.queryParam['beginDate'] = this.searchParam.beginDate = dates.start;
      this.tableOpt.queryParam['endDate'] = this.searchParam.endDate = dates.end;
      this.tableOpt.queryParam['orgNo'] = this.searchParam.orgNo = this.row.val.orgNo;
      this.tableShow = true;
    }
  }
</script>
<style lang="scss">
.EA-info{
    .topChartCon{
        width: 100%;
        height: 13em;
        display: flex;
        &>div{
            height: 100%;
            position: relative;
        }
        .basic-info{
            flex: 2;
            .head-title{
                img{
                    vertical-align: sub;
                }
            }
            .detail{
                margin-top: 2em;
                width: 100%;
                li{
                    display: flex;
                    width: 100%;
                    height: 3em;
                    line-height: 3em;
                }
                .title{
                    flex: 3;
                    text-align: right;
                }
                .value{
                    flex: 9;
                    padding-left: 1em;
                    text-align: left;
                }
            }
        }
        .pie{
            flex: 3;
            border-left: 1px solid;
            border-right: 1px solid;
            border-color: rgba(255,255,255,0.15);
            p{
                text-align: center;
                font-size: 1em;
                margin: 0;
                span{
                    color: #B87918;
                    display: inline-block;
                    margin-left: 2em;
                }
            }
        }
        .bar{
            flex: 5;
            .title{
                position: absolute;
                z-index: 10;
                right: 2em;
                top: -0.75em;
                border: 1px solid #0276D9;
                border-radius: 5px;
                .action{
                    background: #0276D9;
                }
                span:nth-child(2){
                    border-right: 1px solid #0276D9;
                    border-left: 1px solid #0276D9;
                }
                span{
                    height: 2em;
                    width: 5em;
                    line-height: 2.2em;
                    text-align: center;
                    float: left;
                    cursor: pointer;
                }
            }
            .mychar{
                width: calc(100% - 1em);
                height: 100%;
            }
        }
    }
    .dialog-img{
        float: right;
        cursor: pointer;
    }
    .EA-content{
        width: 100%;
        height: 100%;
        position: relative;
        .table-bottom{
            padding-bottom: 0;
        }
        .con-box{
            display: flex;
            width: 100%;
            position: relative;
            .detailContent{
                margin:0.5em 15px 0 0 !important;
                margin-top:0em;
                flex: 5;
                padding-left: 0 !important;
                position: relative;
                z-index: 10;
                height: 35em;
                min-height: 470px;
                .loadingBack{
                    width: 100%!important;
                }
            }
            .box-right{
                flex: 5;
            }
            .bg{
                background: rgba(0,84,171,0.4);
                border: 2px solid #217CDA;
                border-right: transparent;
                width: 100%;
                position: absolute;
                left: 0;
                height: 2em;
                padding: 11px 0;
                top: 2.5em;
                border-radius: 3px;
                cursor: pointer;
            }
            .bg:before{
                content: "";
                position: absolute;
                left: -3px;
                top: 1.1em;
                border: 0.7em solid transparent;
                border-left: 0.8em solid #fff;
            }
        }
    }
    .all_foot_con {
        border-radius: 2px;
        position: relative;
        float: left;
        margin-bottom: 1.2em;
        height: calc(100% - 4.5em);
        width: 100%;
        border-bottom: 1px solid #4A5671;
        .all_foot_ta {
            width: 100%;
            text-align: center;
            line-height: 1.4em;
            height: 1.4em;
            margin-bottom:0;
            margin-left: 0;
            overflow: hidden;
        }
        .all_foot_ta td {
            padding: 0.7em 0;
            border-right: 1px solid #4A5671;
            font-size:0.9em;
            border-bottom:1px solid #4A5671;
        }
        .all_foot_ta td:first-child{
            border-top: 1px solid transparent;
            border-left: 1px solid #4A5671;
        }
        .all_foot_ta tr{
            padding: 5px 0
        }
        .tab-tr tr{
            cursor: pointer;
        }
    }
    .bg-Bridge{
        background: #143B72;
        border: 2px solid #217CDA;
        border-right: none;
        border-left: none;
        width: 17px;
        position: fixed;
        height: 2em;
        padding: 11px 0;
        right: 35.7em;
    }
    .box-right{
        background: #143B72;
        border: 1px solid #217CDA;
        margin: 0.5em 0;
        height: 34em;
        min-height: 460px;
        overflow: hidden;
        position: relative;
        .all_foot_con{
            margin: 0 1em;
            width: calc(100% - 2em);
            height: calc(50% - 1em);
            overflow: hidden;
            border: 2px solid #99AAC2;
            .all_foot_ta td {
                padding: 0.7em 0;
                border-right: 1px solid #99AAC2;
                font-size:0.9em;
                border-bottom:1px solid #99AAC2;
            }
            .all_foot_ta td:first-child{
                border-left: 1px solid transparent;
            }
            .all_foot_ta td:last-child{
                border-right: none;
            }
        }
        .lineChar{
            width: 100%;
            height: 50%;
            margin: 0;
        }
        .nodata-Char{
            width: 100%;
            text-align: center;
            font-size: 0.8em;
            height: 50%;
            position: absolute;
            top: 0;
            background: #143B72;
        }
    }
}
</style>



